import { Reducer } from 'redux';
import createSwitch from '../utils/switchAction'
import ACTION_TYPES from '../actions/actionTypes'
import * as actions from '../actions/actions'

export interface MyBlogState {
  markDownPages: any
}

const initState: MyBlogState = {
  markDownPages: ''
}

const setMarkdownPages = (state: MyBlogState, { payload }: actions.SetMarkdownPagesAction): MyBlogState => {
  const { markDownPages } = payload
  console.log('SET_MARKDOWN_PAGES HAD BEEN CALLED !!!')
  return {
    ...state,
    markDownPages: markDownPages
  }
}

const actionSwitch = createSwitch({
  [ACTION_TYPES.SET_MARKDOWN_PAGES]: setMarkdownPages
})

const Myblog: Reducer<MyBlogState> = (state: MyBlogState = initState, action): MyBlogState => actionSwitch(state, action)

export default Myblog
