import { SagaIterator } from 'redux-saga-ie8'
import { put, call } from 'redux-saga-ie8/effects'
import { loadMd } from '../markdown-pages/markdown_handler'
import ACTION_TYPES from '../actions/actionTypes'
import * as actions from '../actions/actions'
import { takeEvery } from 'redux-saga-ie8'

function* fetchMarkdownPages(): SagaIterator {
  try {
    const mds = yield call(loadMd)
    yield put(actions.setMarkdownPages(mds))
  } catch(e) {
    console.log(e)
  }
}

export default function * root() {
  yield [
    takeEvery(ACTION_TYPES.FETCH_MARKDOWN_PAGES, fetchMarkdownPages)
  ]
}