import ACTION_TYPES from './actionTypes'
import { Action } from 'redux'

/**
 * 讀取md檔案
 */
export function fetchMarkdownPages(): Action<string> {
  return {
    type: ACTION_TYPES.FETCH_MARKDOWN_PAGES
  }
}

export interface SetMarkdownPagesAction extends Action<string> {
  payload: {
    markDownPages: any
  }
}

/**
 * 設定md資料
 * @param {object} markDownPages .md
 */
export function setMarkdownPages(markDownPages: any): SetMarkdownPagesAction {
  return {
    type: ACTION_TYPES.SET_MARKDOWN_PAGES,
    payload: { markDownPages }
  }
}
