import React, { ReactElement } from 'react'
import { Layout, Button, Breadcrumb, Menu, Icon, Affix } from 'antd';
import { useMyBlog } from '../hooks/myBlog'
import ReactMarkdown from 'react-markdown'

const { Header, Content, Footer, Sider } = Layout;

export default function Myblog(): ReactElement {
  const { markDownPages, setMarkdownPages, fetchMarkdownPages } = useMyBlog()
  return (
    <>
      <Layout style={{ minHeight: '100vh' }}>
        <Header style={{ background: '#fff', padding: 0 }} />
        <Sider theme='light'>
          <Affix>
            <Menu theme='light' mode="inline">
              <Menu.Item key="1">
                <Icon type="pie-chart" />
                <span>Option 1</span>
              </Menu.Item>
              <Menu.Item key="2">
                <Icon type="desktop" />
                <span>Option 2</span>
              </Menu.Item>
            </Menu>
          </Affix>
        </Sider>
        <Layout>
          <Breadcrumb style={{ margin: '16px' }}>
            <Breadcrumb.Item>User</Breadcrumb.Item>
            <Breadcrumb.Item>Bill</Breadcrumb.Item>
          </Breadcrumb>
          <Content style={{ margin: '0 16px' }}>
            <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
              <ReactMarkdown source={markDownPages} />
            </div>
            <Button onClick={() => setMarkdownPages('x')}>xx</Button>
            <Button onClick={() => fetchMarkdownPages()}>yy</Button>
          </Content>
          <Footer style={{ textAlign: 'center' }}>Ant Design ©2018 Created by Ant UED</Footer>
        </Layout>
      </Layout>
    </>
  )
}
