// import { useEffect } from 'react'
import { useDispatch, useMappedState } from 'redux-react-hook'
import { setMarkdownPages, fetchMarkdownPages } from '../actions/actions'
import { bindActionCreators } from 'redux'
// import _ from 'lodash'

export function useMyBlog(): {
  markDownPages: any
  setMarkdownPages: (markDownPages: any) => void
  fetchMarkdownPages: () => void
} {
  const state = useMappedState(s => s)
  const dispatch = useDispatch()
  const markDownPages = state.markDownPages

  const actionCreators = bindActionCreators(
    { setMarkdownPages, fetchMarkdownPages },
    dispatch
  )

  return {
    markDownPages,
    ...actionCreators
  }
}